import React from "react";
import Item from "./Item";

const ItemList = ({ prods }) => {
  return (    
    <div className="col-md-9 offset-md-3 productos" id="listadoProductos">
        {prods.map((prod) => (
          <Item key={prod.sku} prod={prod} />
        ))}
    </div>
  );
};

export default ItemList;